import React, { Component } from 'react';
import { FormattedMessage } from 'react-intl';
import { connect } from 'react-redux';
import { languages, CRUD_ACTIONS } from '../../../../utils/constant';
import * as actions from '../../../../store/actions';
import Loading from '../Loading';

import * as EmailValidator from 'email-validator';
import { CommonUtils } from '../../../../utils';

import Lightbox from 'react-image-lightbox';
import 'react-image-lightbox/style.css';

import TableUserRedux from './Table/tableUserRedux';

import './User-redux.scss';
import ConvertBase64Image from '../converBase64/convertBase64';
import MarkDownRender from '../markDown/markDown';

class UserRedux extends Component {
    constructor(props) {
        super(props);
        this.state = {
            IDEDIT: '',
            actions: CRUD_ACTIONS.CREATE,
            genderArr: [],
            positionsArr: [],
            rolesArr: [],
            link: '',
            photoIndex: 0,
            isOpen: false,
            email: '',
            password: '',
            firstName: '',
            lastName: '',
            phonenumber: '',
            address: '',
            gender: '',
            position: '',
            roleId: '',
            avatar: '',
        };
    }

    async componentDidMount() {
        await this.props.getGenderStart();
        await this.props.getPositionStart();
        await this.props.getRoleStart();
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        if (prevProps.genders !== this.props.genders) {
            const data = this.props.genders;

            if (data && data.length > 0) {
                this.setState({
                    genderArr: data,
                    gender: data[0].key,
                });
            }
        }

        if (prevProps.positions !== this.props.positions) {
            const data = this.props.positions;

            if (data && data.length > 0) {
                this.setState({
                    positionsArr: data,
                    position: data[0].key,
                });
            }
        }

        if (prevProps.roles !== this.props.roles) {
            const data = this.props.roles;

            if (data && data.length > 0) {
                this.setState({
                    rolesArr: data,
                    roleId: data[0].key,
                });
            }
        }

        if (prevProps.users !== this.props.users) {
            const genderData = this.props.genders;
            const positionsData = this.props.positions;
            const rolesData = this.props.roles;

            if (
                genderData &&
                positionsData &&
                rolesData &&
                genderData.length > 0 &&
                positionsData.length > 0 &&
                rolesData.length > 0
            ) {
                this.setState({
                    email: '',
                    password: '',
                    firstName: '',
                    lastName: '',
                    phonenumber: '',
                    address: '',
                    gender: genderData[0].keyMap,
                    position: positionsData[0].keyMap,
                    roleId: rolesData[0].keyMap,
                    avatar: '',
                    actions: CRUD_ACTIONS.CREATE,
                    link: '',
                });
            }
        }
    }

    checkValidateInput() {
        const ArrCheck = [
            'email',
            'password',
            'firstName',
            'lastName',
            'phonenumber',
            'address',
            'gender',
            'position',
            'roleId',
        ];

        let isValid = true;

        if (EmailValidator.validate(this.state['email'])) {
            for (let i = 0; i < ArrCheck.length; i++) {
                if (!this.state[ArrCheck[i]]) {
                    isValid = false;
                    alert('Missing required parameter ' + ArrCheck[i]);
                    break;
                }
            }
        } else {
            alert('required parameter @gmail.com');
            isValid = false;
        }

        if (this.state['password'].length > 0) {
            if (this.state['password'].length >= 8) {
                isValid = true;
            } else {
                alert('required parameter 8 key');
                isValid = false;
            }
        }

        return isValid;
    }

    handleSaveUser(e) {
        e.preventDefault();

        const { actions } = this.state;

        if (actions === CRUD_ACTIONS.CREATE) {
            const checkInvalid = this.checkValidateInput();

            if (checkInvalid) {
                this.props.createNewUser(this.state);
            }
        }

        if (actions === CRUD_ACTIONS.EDIT) {
            const {
                IDEDIT,
                actions,
                email,
                password,
                firstName,
                lastName,
                phonenumber,
                address,
                gender,
                position,
                roleId,
                avatar,
            } = this.state;

            this.props.editUserRedux({
                id: IDEDIT,
                actions,
                email,
                password,
                firstName,
                lastName,
                phonenumber,
                address,
                gender,
                positionId: position,
                roleId,
                image: avatar,
            });
        }
    }

    onChangeInput(e, id) {
        const cloneState = { ...this.state };

        cloneState[id] = e.target.value;

        this.setState({
            ...cloneState,
        });
    }

    handleEditUserFromParents = (user) => {
        const imageBase64 = ConvertBase64Image(user.image);

        this.setState({
            email: user.email,
            password: 'API Khng tr v password nn ng n f12 thay i type',
            firstName: user.firstName,
            lastName: user.lastName,
            phonenumber: user.phonenumber,
            address: user.address,
            gender: user.gender,
            position: user.positionId,
            roleId: user.roleId,
            actions: CRUD_ACTIONS.EDIT,
            IDEDIT: user.id,
            link: imageBase64,
        });
    };

    async handleOnchangeImage(e) {
        const file = e.target.files[0];

        let checkFileImage = true;

        if (file) {
            if (file.type !== 'image/jpeg' && file.type !== 'image/png') {
                window.alert('Please upload a file image image/jpeg');
                return (checkFileImage = false);
            }

            if (file.size > 2000000) {
                window.alert('Please upload a file smaller than 2 MB');
                return (checkFileImage = false);
            }

            if (checkFileImage) {
                const base64 = await CommonUtils.convertBase64(file);

                const fileA = URL.createObjectURL(file);

                this.setState({
                    link: fileA,
                    avatar: base64,
                });
            }
        }
    }

    render() {
        const images = [
            this.state.link ||
                'data:image/png;base64,',
        ];

        const {
            genderArr,
            positionsArr,
            rolesArr,
            photoIndex,
            isOpen,
            email,
            password,
            firstName,
            lastName,
            phonenumber,
            address,
            gender,
            position,
            roleId,
            // avatar,
        } = this.state;

        const languageProps = this.props.language;

        const isLoading = this.props.isLoading;

        return (
            <>
                <div className="user-redux-container container py-3">
                    {isLoading && (
                        <div>
                            <Loading />
                        </div>
                    )}

                    <div className="title text-center">
                        <FormattedMessage id="tableAdmin.titleUserRedux" />
                    </div>
                    <div className="body-redux-user">
                        <h5 className="text">
                            <FormattedMessage id="manage-user-redux.add" />
                        </h5>

                        <div className="row">
                            <form className="row g-3">
                                <div className="col-12 col-md-3">
                                    <label htmlFor="inputEmail4" className="form-label">
                                        <FormattedMessage id="manage-user-redux.email" />
                                    </label>
                                    <input
                                        disabled={this.state.actions === CRUD_ACTIONS.EDIT ? true : false}
                                        type="email"
                                        className="form-control"
                                        id="inputEmail4"
                                        value={email}
                                        onChange={(e) => this.onChangeInput(e, 'email')}
                                        placeholder="truongsonpt@gmail.com"
                                    />
                                </div>
                                <div className="col-12 col-md-3">
                                    <label htmlFor="inputPassword4" className="form-label">
                                        <FormattedMessage id="manage-user-redux.password" />
                                    </label>
                                    <input
                                        disabled={this.state.actions === CRUD_ACTIONS.EDIT ? true : false}
                                        type="password"
                                        className="form-control"
                                        id="inputPassword4"
                                        value={password}
                                        onChange={(e) => this.onChangeInput(e, 'password')}
                                        placeholder="Nhp mt khu ca bn"
                                    />
                                </div>
                                <div className="col-12 col-md-3">
                                    <label htmlFor="inputEmail43" className="form-label">
                                        <FormattedMessage id="manage-user-redux.firstName" />
                                    </label>
                                    <input
                                        type="text"
                                        className="form-control"
                                        id="inputEmail43"
                                        value={firstName}
                                        onChange={(e) => this.onChangeInput(e, 'firstName')}
                                        placeholder="eg: Nguyen"
                                    />
                                </div>
                                <div className="col-12 col-md-3">
                                    <label htmlFor="inputEmail45" className="form-label">
                                        <FormattedMessage id="manage-user-redux.lastName" />
                                    </label>
                                    <input
                                        type="text"
                                        className="form-control"
                                        id="inputEmail45"
                                        placeholder="eg: Son"
                                        value={lastName}
                                        onChange={(e) => this.onChangeInput(e, 'lastName')}
                                    />
                                </div>

                                <div className="col-12 col-md-3">
                                    <label htmlFor="phonenumber" className="form-label">
                                        <FormattedMessage id="manage-user-redux.phonenumber" />
                                    </label>
                                    <input
                                        type="text"
                                        className="form-control"
                                        id="phonenumber"
                                        placeholder="+ 84 ...."
                                        value={phonenumber}
                                        onChange={(e) => this.onChangeInput(e, 'phonenumber')}
                                    />
                                </div>

                                <div className="col-12 col-md-9">
                                    <label htmlFor="inputAddress" className="form-label">
                                        <FormattedMessage id="manage-user-redux.address" />
                                    </label>
                                    <input
                                        type="text"
                                        className="form-control"
                                        id="inputAddress"
                                        placeholder="1234 Main St"
                                        value={address}
                                        onChange={(e) => this.onChangeInput(e, 'address')}
                                    />
                                </div>
                                <div className=" col-12 col-md-3">
                                    <label htmlFor="inputSEX" className="form-label">
                                        <FormattedMessage id="manage-user-redux.gennder" />
                                    </label>
                                    <select
                                        id="inputSEX"
                                        className="form-select"
                                        value={gender}
                                        onChange={(e) => this.onChangeInput(e, 'gender')}
                                    >
                                        {genderArr &&
                                            genderArr.length > 0 &&
                                            genderArr.map((data, index) => (
                                                <option key={index} value={data.keyMapMap}>
                                                    {languageProps === languages.VI ? data.valueVI : data.valueEN}
                                                </option>
                                            ))}
                                    </select>
                                </div>
                                <div className=" col-12 col-md-3">
                                    <label htmlFor="inputPositions" className="form-label">
                                        <FormattedMessage id="manage-user-redux.position" />
                                    </label>
                                    <select
                                        id="inputPositions"
                                        className="form-select"
                                        value={position}
                                        onChange={(e) => this.onChangeInput(e, 'position')}
                                    >
                                        {positionsArr &&
                                            positionsArr.length > 0 &&
                                            positionsArr.map((data, index) => (
                                                <option key={index} value={data.keyMap}>
                                                    {languageProps === languages.VI ? data.valueVI : data.valueEN}
                                                </option>
                                            ))}
                                    </select>
                                </div>
                                <div className=" col-12 col-md-3">
                                    <label htmlFor="inputVaitro" className="form-label">
                                        <FormattedMessage id="manage-user-redux.role" />
                                    </label>
                                    <select
                                        id="inputVaitro"
                                        className="form-select"
                                        value={roleId}
                                        onChange={(e) => this.onChangeInput(e, 'roleId')}
                                    >
                                        {rolesArr &&
                                            rolesArr.length > 0 &&
                                            rolesArr.map((data, index) => (
                                                <option key={index} value={data.keyMap}>
                                                    {languageProps === languages.VI ? data.valueVI : data.valueEN}
                                                </option>
                                            ))}
                                    </select>
                                </div>
                                <div className="col-12 col-md-3">
                                    <label htmlFor="inputADD" className="form-label">
                                        <FormattedMessage id="manage-user-redux.avatar" />
                                    </label>
                                    <input
                                        type="file"
                                        className="form-control"
                                        id="inputADD"
                                        placeholder="Apartment, studio, or floor"
                                        onChange={(e) => this.handleOnchangeImage(e)}
                                    />
                                    <div
                                        className="image-preview"
                                        style={{
                                            backgroundImage: `url(${
                                                this.state.link ||
                                                'data:image/png;base64,'
                                            })`,
                                        }}
                                        onClick={() => this.setState({ isOpen: true })}
                                    ></div>
                                    {/* Preview anh */}

                                    <div>
                                        {isOpen && (
                                            <Lightbox
                                                mainSrc={images[photoIndex]}
                                                onCloseRequest={() => this.setState({ isOpen: false })}
                                            />
                                        )}
                                    </div>
                                </div>
                                <div className="col-12 mt-3">
                                    <button
                                        type="submit"
                                        className="btn btn-primary px-3"
                                        onClick={(e) => this.handleSaveUser(e)}
                                    >
                                        {this.state.actions === 'EDIT' ? (
                                            <FormattedMessage id="actions.btnEdit" />
                                        ) : (
                                            <FormattedMessage id="manage-user-redux.btnSave" />
                                        )}
                                    </button>
                                </div>
                            </form>
                        </div>
                    </div>
                    <TableUserRedux
                        handleEditUserFromParents={this.handleEditUserFromParents}
                        actions={this.state.actions}
                    />
                    <MarkDownRender />
                </div>
            </>
        );
    }
}

const mapStateToProps = (state) => {
    return {
        language: state.app.language,
        genders: state.admin.genders,
        positions: state.admin.positions,
        roles: state.admin.roles,
        isLoading: state.admin.isLoading,
        users: state.admin.users,
    };
};

const mapDispatchToProps = (dispatch) => {
    return {
        getGenderStart: () => dispatch(actions.fetChGenderStart()),
        getPositionStart: () => dispatch(actions.fetChPositionStart()),
        getRoleStart: () => dispatch(actions.fetChRoleStart()),
        createNewUser: (state) => dispatch(actions.createUserRedux(state)),
        fetAllUserWithRedux: () => dispatch(actions.fetchAllUserStart()),
        editUserRedux: (data) => dispatch(actions.editUserRedux(data)),
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(UserRedux);
